import './EditDesign.scss'
import $ from 'jquery'
import { CheckImageURL } from '../resources/Helpers'
import { streamer } from '../resources/Config'
import { db } from '../resources/Firebase'

const EditDesign = () => {

    function guardarNota() {
        let nota = $('#input-nota').val()
        db.collection("users")
        .doc(streamer)
        .update({
            note: nota
        })
        .then(() => {
            console.log('actualizado');
            $('#input-nota').val('')
            $('#msg-form').text('Nota añadida').addClass('success-form')
        })
    }

    function guardarFondo() {
        let fondo = $('#input-fondo').val()
        let check = CheckImageURL(fondo)
    
        if (check) {
            db.collection("users")
            .doc(streamer)
            .update({
                background: fondo
            })
            .then(() => {
                console.log('actualizado');
                $('#input-fondo').val('')
                $('#msg-form').text('Fondo aplicado').addClass('success-form')
                window.location.reload();
            })
        } else {
            $('#msg-form').text('Formato incompatible (.jpeg .jpg .gif .png)').addClass('error-form')
        }
    }

    function guardarLogo() {
        let logo = $('#input-logo').val()
        let check = CheckImageURL(logo)
    
        if (check) {
            db.collection("users")
            .doc(streamer)
            .update({
                logo: logo
            })
            .then(() => {
                console.log('actualizado');
                $('#input-logo').val('')
                $('#msg-form').text('Logo aplicado').addClass('success-form')
                window.location.reload();
            })
        } else {
            $('#msg-form').text('Formato incompatible (.jpeg .jpg .gif .png)').addClass('error-form')
        }
    }
    
    return (
        <>
            <h5 className="text-light titulo-h4 mt-4">Diseño</h5>
            {/* <div className="title-input">Color principal</div>
            <div className="d-flex">
                <input className="form-control form-control-sm" id="input-color" type="color" name="color" />
                <button className="btn btn-1 btn-sm ml-2" id="addcolor" onClick={guardarColor}>Aplicar</button>
            </div> */}
            <div className="title-input">Posit</div>
            <div className="d-flex">
                <input className="form-control form-control-sm" id="input-nota" type="text" name="nota" placeholder="Texto del posit..." />
                <button className="btn btn-1 btn-sm ml-2" id="addnota" onClick={guardarNota}>Aplicar</button>
            </div>
            <div className="title-input">Logo - Formatos: .jpeg .jpg .gif .png</div>
            <div className="d-flex">
                <input className="form-control form-control-sm" id="input-logo" type="URL" name="logo" placeholder="https://..." />
                <button className="btn btn-1 btn-sm ml-2" id="addlogo" onClick={guardarLogo}>Aplicar</button>
            </div>
            <div className="title-input">Fondo - Formatos: .jpeg .jpg .gif .png</div>
            <div className="d-flex">
                <input className="form-control form-control-sm" id="input-fondo" type="URL" name="fondo" placeholder="https://..." />
                <button className="btn btn-1 btn-sm ml-2" id="addfondo" onClick={guardarFondo}>Aplicar</button>
            </div>
            <div className="small mt-2" id="msg-form"></div>
        </>
    )
}

export default EditDesign;